import React from "react";
import Card from "../UI/Card";
import css from "./Display.module.css";

function DisplayZip(props) {
  const icon = props.zipData;

  return (
    <Card weather={icon}>
      <div>
        <ul>
          {props.zipData.map((item) => (
            <li style={{ lineHeight: 1 }} className={css.list} key={item.Id}>
              <div key={item.Id}>
                <p className={css.other} style={{ paddingTop: 40 }}>
                  {item.Clouds}
                </p>
                <p className={css.other} style={{ paddingTop: 100 }}>
                  {item.Location}
                </p>
                <p className={css.temp} style={{ fontSize: "3.5rem" }}>
                  {item.Temperature}¬∞C
                </p>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </Card>
  );
}

export default DisplayZip;
