import React, { useState } from "react";
import { Collapse, Navbar, NavbarToggler, Nav, NavItem } from "reactstrap";
import { Link } from "react-router-dom";
import classes from "./Navbar.module.css";
import logo from "../Images/weather icon.png";

const NavBar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar light className={classes.navAdjust} expand="md" fixed="top">
        <Link to="/" className={classes.brand}>
          <img
            style={{ width: 60, borderRadius: 10 }}
            src={logo}
            alt="Weather Icon"
          />
        </Link>

        <NavbarToggler onClick={toggle} />
        <Collapse className={classes.open} isOpen={isOpen} navbar>
          <Nav onClick={toggle} className={classes.adjust} navbar>
            <NavItem>
              <Link to="/">Current Location</Link>
            </NavItem>
            <NavItem>
              <Link to="/zip-code">Zip Code</Link>
            </NavItem>
            <NavItem>
              <Link to="/location">By Location</Link>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
};

export default NavBar;
