import React from "react";
import { useRef, useState } from "react";
import css from "./ZipCode.module.css";
import { Row, Col, Container } from "reactstrap";
import More from "./More";

function LocationCity() {
  const [card, setCard] = useState(false);
  const [data, setData] = useState([]);
  const city = useRef();

  function getWeather() {
    const enteredCity = city.current.value;
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=  ${enteredCity}&units=metric&appid=57cf814cd232ff467d8a4c2d7dfee6c7`
    )
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        const dataContainer = [];
        let oldClouds = data.weather[0].description;
        let upperCase = oldClouds.slice(0, 1);
        let upper = upperCase.toUpperCase();
        let Clouds = oldClouds.replace(upperCase, upper);

        const information = {
          Id: data.id,
          Location: data.name,
          Humidity: data.main.humidity,
          Clouds,
          "Feels Like": data.main.feels_like,
          Temperature: data.main.temp,
          Low: data.main.temp_min,
          High: data.main.temp_max,
          "Wind Speed": data.wind.speed,
          Icon: data.weather[0].icon,
        };

        dataContainer.push(information);
        console.log(information);
        console.log(data);
        setData(dataContainer);
        setCard(true);
      })
      .catch((err) => {
        alert(` Try Again`);
        setCard(false);
        city.current.value = "";
      });
  }
  return (
    <div className={css.body}>
      <Container>
        <Row>
          <Col style={{ paddingBottom: 25 }} md="6">
            {" "}
            <label>Enter City</label>
            <input
              className={css.input}
              required
              ref={city}
              placeholder="Enter City"
            />
            <button
              style={{ marginTop: "40px" }}
              className="btn btn-primary"
              onClick={() => getWeather()}
            >
              Weather Report
            </button>
          </Col>
          <Col md="6">
            {" "}
            <h1> Weather Information</h1>
            {card === true ? <More more={data} /> : ""}
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default LocationCity;
