import React from "react";
import css from "./ZipCode.module.css";
import { useRef, useState } from "react";
import { Row, Col, Container } from "reactstrap";
import More from "./More";

function ZipCode() {
  let zipCode = useRef();

  const [card, setCard] = useState(false);
  const [data, loadedData] = useState([]);

  function displayLocation() {
    const enteredZipCode = zipCode.current.value;
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?zip=${enteredZipCode}&units=metric&appid=57cf814cd232ff467d8a4c2d7dfee6c7`
    )
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        const infos = [];
        let oldClouds = data.weather[0].description;
        let upperCase = oldClouds.slice(0, 1);
        let upper = upperCase.toUpperCase();
        let Clouds = oldClouds.replace(upperCase, upper);

        const information = {
          Id: data.id,
          Location: data.name,
          Country: data.sys.country,
          Humidity: data.main.humidity,
          Clouds,
          "Feels Like": data.main.feels_like,
          Temperature: data.main.temp,
          Low: data.main.temp_min,
          High: data.main.temp_max,
          "Wind Speed": data.wind.speed,
          Icon: data.weather[0].icon,
        };
        infos.push(information);
        loadedData(infos);
        console.log(data);
        console.log(infos);
        setCard(true);
      })
      .catch(() => {
        alert("City Not Found");
        setCard(false);
        zipCode.current.value = "";
      });
  }

  return (
    <div className={css.body}>
      <Container>
        <Row>
          <Col style={{ paddingBottom: 25 }} md="6">
            {" "}
            <label>Zip Code</label>
            <input
              placeholder="Enter Zip Code"
              className={css.input}
              required
              ref={zipCode}
            />
            <input
              className={css.button}
              type="button"
              value="Weather Report"
              onClick={() => displayLocation()}
            />
          </Col>
          <Col md="6">
            <h1>{data.map((item) => item.Country)} Weather Information</h1>
            {card === true ? <More more={data} /> : ""}
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default ZipCode;
