import { Route, Switch } from "react-router-dom";
import GetLocation from "./Components/Pages/GetLocation";
import Navbar from "./Components/Navbar";
import ZipCode from "./Components/Pages/ZipCode";
import LocationCity from "./Components/Pages/LocationCity";

function App() {
  return (
    <div>
      <Navbar></Navbar>
      <Switch>
        <Route path="/" exact>
          <GetLocation />
        </Route>
        <Route path="/zip-code">
          <ZipCode />
        </Route>
        <Route path="/location">
          <LocationCity />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
