import React, { useState } from "react";
import Display from "./Display";
import Card from "../UI/Card";
import css from "../Pages/GetLocation.module.css";
import { Row, Col, Container } from "reactstrap";
import MoreDays from "./MoreDays";

function GetLocation() {
  const [lat, setLat] = useState(null);
  const [lng, setLng] = useState(null);
  const [status, setStatus] = useState(null);
  const [data, loadedData] = useState([]);
  const [card, setCard] = useState(false);
  const [showmore, setShowmore] = useState(false);
  const [showbtn, setShowbtn] = useState(false);
  const [dailyReport, setDailyReport] = useState([]);
  const [moreDays, setMoreDays] = useState(false);

  const getLocationdata = () => {
    if (!navigator.geolocation) {
      setStatus("Geolocation is not supported by your browser");
    } else {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setStatus(null);
          setLat(position.coords.latitude);
          setLng(position.coords.longitude);
          setShowbtn(true);
        },
        () => setStatus("Unable to retrieve your location")
      );
    }
  };
  getLocationdata();

  function displayLocation() {
    fetch(
      `https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lng}&exclude={part}&units=metric&appid=57cf814cd232ff467d8a4c2d7dfee6c7`
    )
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        setCard(true);
        const infos = [];
        const dailyReport = data.daily;
        setDailyReport(dailyReport);
        console.log(dailyReport);
        let oldClouds = data.current.weather[0].description;
        let upperCase = oldClouds.slice(0, 1);
        let upper = upperCase.toUpperCase();
        let Clouds = oldClouds.replace(upperCase, upper);
        const information = {
          Id: data.current.weather[0].id,
          Timezone: data.timezone,
          Humidity: data.current.humidity,
          Clouds,
          "Feels Like": data.current.feels_like,
          Temperature: data.current.temp,
          "Wind Speed": data.current.wind_speed,
          Icon: data.current.weather[0].icon,
        };
        infos.push(information);
        loadedData(infos);
        console.log(data);
        console.log(infos);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <div className={css.body}>
      <Container>
        <Row>
          <Col md="12">
            <h1>{data.map((item) => item.Timezone)} Weather Information</h1>
            {showbtn === true ? (
              <button className={css.button} onClick={displayLocation}>
                Weather Report
              </button>
            ) : (
              <p style={{ fontSize: "1.5rem" }}>üåç Getting Location... üåç</p>
            )}

            {card ? (
              <div>
                <Card weather={data}>
                  <p>{status}</p>
                  <Display info={data} />
                </Card>
                <button
                  className={css.button}
                  onClick={() => setShowmore(!showmore)}
                >
                  {showmore ? "Hide" : "Show More"}
                </button>
                {showmore === true ? (
                  <div>
                    {data.map((item) => (
                      <li className={css.showmore} key={item.Id}>
                        {
                          <div>
                            <p>Feels Like: {item["Feels Like"]}</p>
                            <p>Humidity: {item.Humidity}</p>
                            <p>Wind Speed: {item["Wind Speed"]}</p>
                          </div>
                        }
                      </li>
                    ))}
                  </div>
                ) : (
                  ""
                )}
              </div>
            ) : (
              ""
            )}
          </Col>
        </Row>
        {card ? (
          <button
            className="btn btn-success"
            onClick={() => setMoreDays(!moreDays)}
          >
            {moreDays ? "hide" : "Show 8 Days Weather Forecast"}
          </button>
        ) : (
          ""
        )}
        {moreDays ? (
          <Row style={{ paddingTop: 30 }}>
            <Col md="12">
              <MoreDays days={dailyReport} />
            </Col>
          </Row>
        ) : (
          ""
        )}
      </Container>
    </div>
  );
}

export default GetLocation;
