import css from "./DailyCard.module.css";

function DailyCards(props) {
  const iconId = props.icon;
  return (
    <div className={css.body}>
      <span className={css.weatherIcon}>
        <img
          src={`http://openweathermap.org/img/wn/${iconId}@2x.png`}
          alt="Weather Icon"
        />
      </span>
      {props.children}
    </div>
  );
}

export default DailyCards;
