import css from "./Card.module.css";

function Card(props) {
  const iconId = props.weather.map((item) => item.Icon);

  return (
    <div className={css.card}>
      <span className={css.weatherIcon}>
        <img
          src={`http://openweathermap.org/img/wn/${iconId}@2x.png`}
          alt="Weather Icon"
        />
      </span>
      {props.children}
    </div>
  );
}

export default Card;
