import React, { useState } from "react";
import DisplayZip from "./DisplayZip";
import css from "./ZipCode.module.css";

function More(props) {
  const data = props.more;
  const [showmore, setShowmore] = useState(false);
  return (
    <div>
      <div>
        <DisplayZip zipData={data} />
        <button className={css.button} onClick={() => setShowmore(!showmore)}>
          {showmore ? "Hide" : "Show More"}
        </button>
        <div>
          {showmore === true ? (
            <div>
              {data.map((item) => (
                <li className={css.showmore} key={item.Id}>
                  {
                    <div>
                      <p>Feels Like: {item["Feels Like"]}</p>
                      <p>Humidity: {item.Humidity}</p>
                      <p>Wind Speed: {item["Wind Speed"]}</p>
                      <p>Low: {item.Low}</p>
                      <p>High: {item.High}</p>
                    </div>
                  }
                </li>
              ))}
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
}

export default More;
